import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useCallback } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';
// material
import { LoadingButton } from '@mui/lab';
import { Icon } from '@iconify/react';
import {
  Box,
  Card,
  Grid,
  Stack,
  Switch,
  TextField,
  Typography,
  FormHelperText,
  FormGroup,
  FormControlLabel,
  Button
} from '@mui/material';
// utils
import { fData } from '../../../utils/formatNumber';
import fakeRequest from '../../../utils/fakeRequest';
// routes
import { PATH_DASHBOARD } from '../../../routes/paths';
//
import Label from '../../Label';
import { UploadAvatar } from '../../upload';
import { DownloadCSVButton, AddNewEmployeeButton } from './';
import { styled } from '@mui/material/styles';

// ----------------------------------------------------------------------

UserNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentUser: PropTypes.object
};

export default function UserNewForm({ isEdit, currentUser }) {
  const navigate = useNavigate();
  // const { enqueueSnackbar } = useSnackbar();

  const NewUserSchema = Yup.object().shape({
    firstname: Yup.string().required('Name is required'),
    lastname: Yup.string().required('Name is required'),
    phoneNumber: Yup.string().required('Phone number is required'),
    email: Yup.string().required('Email is required').email(),
    role: Yup.string().required('Role is required'),
    assistant: Yup.string().required('Assistant is required'),
    blank: Yup.string().required('"" is required'),
    avatarUrl: Yup.mixed().required('Avatar is required')
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      firstname: currentUser?.name || '',
      lastname: currentUser?.name || '',
      phoneNumber: currentUser?.phoneNumber || '',
      email: currentUser?.email || '',
      role: currentUser?.role || '',
      assistant: currentUser?.assistant || '',
      blank: currentUser?.blank || '',
      avatarUrl: currentUser?.avatarUrl || null
    },
    validationSchema: NewUserSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      try {
        await fakeRequest(500);
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.user.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    }
  });

  const { errors, values, touched, handleSubmit, isSubmitting, setFieldValue, getFieldProps } = formik;

  const handleDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue('avatarUrl', {
          ...file,
          preview: URL.createObjectURL(file)
        });
      }
    },
    [setFieldValue]
  );

  const IOSSwitch = styled((props) => <Switch focusVisibleClassName=".Mui-focusVisible" disableRipple {...props} />)(
    ({ theme }) => ({
      width: 42,
      height: 26,
      padding: 0,
      '& .MuiSwitch-switchBase': {
        padding: 0,
        margin: 2,
        transitionDuration: '300ms',
        '&.Mui-checked': {
          transform: 'translateX(16px)',
          color: '#fff',
          '& + .MuiSwitch-track': {
            backgroundColor: theme.palette.mode === 'dark' ? '#2ECA45' : '#916A00',
            opacity: 1,
            border: 0
          },
          '&.Mui-disabled + .MuiSwitch-track': {
            opacity: 0.5
          }
        },
        '&.Mui-focusVisible .MuiSwitch-thumb': {
          color: '#33cf4d',
          border: '6px solid #fff'
        },
        '&.Mui-disabled .MuiSwitch-thumb': {
          color: theme.palette.mode === 'light' ? theme.palette.grey[100] : theme.palette.grey[600]
        },
        '&.Mui-disabled + .MuiSwitch-track': {
          opacity: theme.palette.mode === 'light' ? 0.7 : 0.3
        }
      },
      '& .MuiSwitch-thumb': {
        boxSizing: 'border-box',
        width: 22,
        height: 22
      },
      '& .MuiSwitch-track': {
        borderRadius: 26 / 2,
        backgroundColor: theme.palette.mode === 'light' ? '#E9E9EA' : '#39393D',
        opacity: 1,
        transition: theme.transitions.create(['background-color'], {
          duration: 500
        })
      }
    })
  );

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Stack spacing={3} sx={{ mt: -10 }}>
          <Stack>
            {isEdit && (
              <Label
                color={values.status !== 'active' ? 'error' : 'success'}
                sx={{ textTransform: 'uppercase', position: 'absolute', top: 24, right: 24 }}
              >
                {values.status}
              </Label>
            )}
            <Typography
              variant="subtitle1"
              sx={{
                fontSize: '32px',
                color: '#00000060',
                fontWeight: '900',
                display: 'flex',
                justifyContent: 'center'
              }}
            >
              Add a Employee
            </Typography>
            <Typography variant="subtitle1" sx={{ fontWeight: 'bold', mt: 3 }}>
              Take Photo
            </Typography>
            <Typography variant="subtitle2">Upload an image to make it easily recognizable.</Typography>

            <Box sx={{ my: 3 }}>
              <UploadAvatar
                accept="image/*"
                file={values.avatarUrl}
                maxSize={3145728}
                onDrop={handleDrop}
                error={Boolean(touched.avatarUrl && errors.avatarUrl)}
                caption={
                  <Box sx={{ mt: 5, display: 'flex', justifyContent: 'center' }}>
                    <Button
                      variant="contained"
                      sx={{
                        color: 'white',
                        backgroundColor: '#916A00',
                        boxShadow: '0 0px 0px #fff',
                        py: '7px',
                        px: '22px',
                        '&:hover': {
                          backgroundColor: '#916A00'
                        },
                        fontSize: '14px',
                        fontWeight: '700'
                      }}
                      startIcon={<Icon icon="mdi:cloud-upload" color="white" width="20" height="20" />}
                    >
                      Take Image
                    </Button>
                  </Box>
                }
              />
            </Box>
          </Stack>

          <Stack spacing={3}>
            <Stack>
              <Typography variant="subtitle1">2. Employee Details</Typography>
              <Typography variant="subtitle2" sx={{ ml: 2 }}>
                Enter applicant details below.
              </Typography>
            </Stack>
            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
              <TextField
                fullWidth
                label="First Name"
                {...getFieldProps('name')}
                error={Boolean(touched.name && errors.name)}
                helperText={touched.name && errors.name}
              />
              <TextField
                fullWidth
                label="Last Name"
                {...getFieldProps('name')}
                error={Boolean(touched.name && errors.name)}
                helperText={touched.name && errors.name}
              />
            </Stack>

            <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ xs: 3, sm: 2 }}>
              <TextField
                fullWidth
                label="Phone Number"
                {...getFieldProps('phoneNumber')}
                error={Boolean(touched.phoneNumber && errors.phoneNumber)}
                helperText={touched.phoneNumber && errors.phoneNumber}
              />
            </Stack>
            <Stack>
              <TextField
                fullWidth
                label="Email Address"
                {...getFieldProps('email')}
                error={Boolean(touched.email && errors.email)}
                helperText={touched.email && errors.email}
              />
            </Stack>
            <Stack>
              <TextField
                fullWidth
                label="Role"
                {...getFieldProps('role')}
                error={Boolean(touched.role && errors.role)}
                helperText={touched.role && errors.role}
              />
            </Stack>
            <Stack>
              <TextField
                fullWidth
                label="Assistant"
                {...getFieldProps('assistant')}
                error={Boolean(touched.assistant && errors.assistant)}
                helperText={touched.assistant && errors.assistant}
              />
            </Stack>

            <Stack>
              <TextField
                fullWidth
                label=""
                {...getFieldProps('blank')}
                error={Boolean(touched.blank && errors.blank)}
                helperText={touched.blank && errors.blank}
              />
            </Stack>

            <Box sx={{ mt: 6, display: 'flex', justifyContent: 'center' }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
                sx={{
                  color: 'white',
                  backgroundColor: '#916A00',
                  boxShadow: '0 0px 0px #fff',
                  py: '7px',
                  px: '22px',
                  mt: 3,
                  '&:hover': {
                    backgroundColor: '#916A00'
                  },
                  fontSize: '14px',
                  fontWeight: '500'
                }}
              >
                Add New Visitor
              </LoadingButton>
            </Box>
          </Stack>

          <Stack spacing={1}>
            <Stack>
              <Typography variant="subtitle1">3. Vaccination Records</Typography>
              <Typography variant="subtitle2" sx={{ ml: 2 }}>
                Upload Vaccination Records
              </Typography>
            </Stack>

            <Stack>
              <Box>
                <DownloadCSVButton />
              </Box>
            </Stack>
          </Stack>

          <Stack spacing={3}>
            <Stack>
              <Typography variant="subtitle1">4. App Settings</Typography>
            </Stack>

            <Stack>
              <FormControlLabel
                control={<IOSSwitch sx={{ m: 1 }} defaultChecked />}
                label="Show employee as a host on Visitor Kiosk"
              />
              <FormControlLabel control={<IOSSwitch sx={{ m: 1 }} />} label="Notify Employee on Mobile App" />
              <FormControlLabel control={<IOSSwitch sx={{ m: 1 }} defaultChecked />} label="Notify Employee on Email" />
              <Box sx={{ mt: 5, display: 'flex', justifyContent: 'center' }}>
                <AddNewEmployeeButton />
              </Box>
            </Stack>
          </Stack>
        </Stack>
      </Form>
    </FormikProvider>
  );
}
