// material
import { Button } from '@mui/material';

export default function AddNewEmployeeButton(props) {
  const { handleModal } = props;

  return (
    <Button
      variant="contained"
      sx={{
        color: '#F6F6F7',
        backgroundColor: '#916A00',
        boxShadow: '0 0px 0px #fff',
        py: '11px',
        px: '22px',
        '&:hover': {
          backgroundColor: '#916A00'
        },
        fontSize: '14px',
        lineHeight: '17px',
        fontWeight: '500',
        fontFamily: 'inter'
      }}
      onClick={handleModal}
    >
      Add New Employee
    </Button>
  );
}
