// material
import { Button } from '@mui/material';
import { Icon } from '@iconify/react';

export default function DownloadCSVButton(props) {
  const { handleModal } = props;

  return (
    <Button
      variant="contained"
      sx={{
        color: '#7E5700',
        backgroundColor: '#F6F6F7',
        boxShadow: '0 0px 0px #fff',
        py: '11px',
        px: '22px',
        '&:hover': {
          backgroundColor: '#F6F6F7'
        },
        fontSize: '14px',
        lineHeight: '17px',
        fontWeight: '500',
        fontFamily: 'inter'
      }}
      onClick={handleModal}
      startIcon={<Icon icon="mdi:tray-arrow-down" color="#7E5700" width="16" height="16" />}
    >
      Download CSV
    </Button>
  );
}
